body {
  margin: 0;
  padding: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  display: flex;
}

.header__logo-image {
  width: auto;
  height: 40px;
}

.header__toggle {
  cursor: pointer;
  background: none;
  border: none;
  font-size: 24px;
  display: none;
}

@media (width <= 768px) {
  .header__toggle {
    display: block;
  }
}

.nav__list {
  gap: 20px;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

@media (width <= 768px) {
  .nav__list {
    flex-direction: column;
    display: none;
  }

  .nav__list.is-open {
    display: flex;
  }
}

.nav__link {
  color: #060b35;
  text-decoration: none;
  transition: color .3s;
}

.nav__link:hover {
  color: #00acdc;
}

.nav__link.is-active {
  color: #00acdc;
  text-decoration: underline;
}

.catalog {
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  gap: 48px 46px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .catalog {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .catalog {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .catalog {
    grid-template-columns: repeat(4, 200px);
  }
}

.card {
  z-index: auto;
  background: #fff;
  border: 1px solid #eee;
  border-radius: 8px;
  width: 200px;
  padding: 20px;
  transition: transform .3s;
}

.card__image {
  object-fit: contain;
  width: 100%;
  height: 160px;
  margin-bottom: 12px;
}

.card__title {
  color: #000;
  margin: 10px 0;
  font-size: 18px;
  transition: color .3s;
}

.card__stars {
  gap: 4px;
  margin-bottom: 10px;
  display: flex;
}

.card__buy {
  color: #fff;
  background-color: #00acdc;
  border: 1px solid #0000;
  border-radius: 4px;
  padding: 8px 16px;
  text-decoration: none;
  transition: all .3s;
  display: inline-block;
}

.card:hover {
  z-index: 1;
  transform: scale(1.2);
}

.card:hover .card__title {
  color: #34568b;
}

.card:hover .card__buy {
  color: #00acdc;
  background: #fff;
  border-color: #00acdc;
}

.star {
  width: 16px;
  height: 16px;
}
